/* Class that accepts a Record<string, string> and returns a HemeSample object.
 The parent class for QC and PA samples.
*/
class HemeSampleItem {
    constructor(sample) {
        this._rawData = sample;
    }
    get data() {
        return this._rawData;
    }
    get id() {
        return this._rawData['id'];
    }
    get label() {
        return this._rawData['label'];
    }
    get subgroups() {
        return this._rawData['subgroups'];
    }
    /** Returns all the items for the subgroup
     * @param sgid
     */
    GetSubgroup(sgid) {
        return this._rawData['subgroups'][sgid];
    }
    get analysisDate() {
        // returns the last analysis date in the analysisdates string
        let analysisdates = this.analysisDates;
        return analysisdates[analysisdates.length - 1];
    }
    get analysisDates() {
        // returns all analysis dates in the analysisdates string
        let analysisdates = this.data.analysisdates.split(/ *; */);
        return analysisdates;
    }
}
exports.HemeSampleItem = HemeSampleItem;
class QCSampleItem extends HemeSampleItem {
    constructor(itemInfo) {
        super(itemInfo);
    }
    setQCFileNo(fileNo) {
        this.data.qcfileno = fileNo;
    }
    get lotNo() {
        return this.data.label;
    }
    get material() {
        let mat = "";
        //console.log(this.GetSubgroup('sampleinfo')['material']['description']);
        mat = this.GetSubgroup('sampleinfo')['material'] ? this.GetSubgroup('sampleinfo')['material']['description'] : "";
        return mat;
    }
    get expiry() {
        let exp = "";
        exp = this.GetSubgroup('sampleinfo')['expirydate'] ? this.GetSubgroup('sampleinfo')['expirydate']['description'] : "";
        return exp;
    }
}
exports.QCSampleItem = QCSampleItem;
